import React, { Component } from 'react';
import ContainerLayout from '../components/containerLayout';
import VideoLoder from '../components/videoLoader';
import importCharts from '../utils/importCharts'
import ChartList from '../components/chartList';

class Charts extends Component {

    constructor(props) {
        super(props);
        this.state = {
            charts: []
        }
    }

    componentDidMount() {
        importCharts((chart) => {
            this.setState((state) => { return { charts: state.charts.concat([chart]) } })
        });
        // const videoLoader = new VideoLoder(this.chartsContainer, ["Ph7iIpjkC0w", "yiANaTpcSEQ", "DqvgP6FfqYU", "PObJuJOXZyo", "1oAz4ViTRlM"])
    }

    render() {
        const { charts } = this.state;
        return (
            <ContainerLayout>
                <h1>Charts</h1>
                {/* <div ref={r => this.chartsContainer = r} className='vrc__playersContainer'/> */}
                <ChartList charts={charts} />
            </ContainerLayout>
        )
    }
};

export default Charts;