import React from 'react';
import { Link } from '@reach/router'

const ChartList = ({ charts, elementsPerRow = 3 }) => {
    const reducedBlogs = charts.reduce((acc, cur) => {
        const index = Object.keys(acc).length || 1;
        if (!acc[index]) {
            acc[index] = [cur]
        } else if (acc[index].length === elementsPerRow) {
            acc[index + 1] = [cur]
        } else {
            acc[index] = acc[index].concat([cur])
        }
        return acc;
    }, {});
    return (
        <div className="container vrc__blogListContainer">
            {Object.keys(reducedBlogs).map(x => (
                <div className="row vrc__blogListRow">
                    {reducedBlogs[x].map(y => (
                        <div className={`col-md-${12 / elementsPerRow} vrc__blogListCol`}>
                            <div className="vrc__blogListCard">
                                <Link to={y.meta.link}>
                                    <div className="vrc__blogListImgWrapper" style={{ backgroundImage: `url(${y.meta.image})` }} />
                                    <div className="vrc__blogCardBody">
                                    <h2>{y.meta.title}</h2>
                                    <span>{y.meta.intro}</span>
                                    </div>
                                </Link>
                            </div >
                        </div>
                    ))}
                </div>
            ))}
        </div>
    )
}

export default ChartList;