const importCharts = (setChartsCallback) => {
    const importAll = (r) => {
        r.keys().forEach(x => {
            var chart = x.substring(2, x.indexOf('.js'));

            import(`../pages/charts/${chart}`).then(res => {
                setChartsCallback(res)
            })
        });
    }

    return importAll(require.context('../pages/charts/', true, /\.js$/));
}

export default importCharts;